import { NavLink } from "react-router-dom";

import { ReactComponent as InstagramIcon } from "../../assets/images/icon/instagram.svg";
import { ReactComponent as FacebookIcon } from "../../assets/images/icon/facebook.svg";
import { ReactComponent as YoutubeIcon } from "../../assets/images/icon/youtube.svg";
import { ReactComponent as UserIcon } from "../../assets/images/icon/user.svg";

import styles from "./contactsBlock.module.scss";

const ContactsBlock = () => {
  return (
    <div className={styles.contactList}>
      <div>
        <a className={styles.tel} href="tel:063-128-4609">
          (063) 128-46-09
        </a>
      </div>
      <div>
        <ul className={styles.contactList}>
          <li className={styles.item}>
            <NavLink>
              <InstagramIcon />
            </NavLink>
          </li>
          <li className={styles.item}>
            <NavLink>
              <FacebookIcon />
            </NavLink>
          </li>
          <li className={styles.item}>
            <NavLink>
              <YoutubeIcon />
            </NavLink>
          </li>
        </ul>
      </div>
      <div className={styles.langBlock}>
        <p className={styles.ukr}>Укр</p>
        <p>Eng</p>
      </div>
      <div className={styles.userBlock}>
        <UserIcon />
        <NavLink to={"/login"} className={styles.link}>
          Вхід
        </NavLink>
      </div>
    </div>
  );
};

export default ContactsBlock;
